* {
  box-sizing: border-box;
}
body {
  margin: 0;
}
a {
  background: transparent;
}
a:focus {
  outline: thin dotted;
}
a:active,
a:hover {
  outline: 0;
}
select:focus,
input:focus,
textarea:focus,
button:focus {
  outline: #5b9dd9 auto;
  outline-offset: -1px;
}
q {
  quotes: "\00ab" "\00bb" "\201e" "\201c";
}
a img {
  border: 0;
}
button,
input,
select,
textarea {
  font-family: inherit;
  color: inherit;
  font-size: 100%;
  margin: 0;
}
button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: none;
  cursor: pointer;
  border: 0;
}
button[disabled],
html input[disabled] {
  cursor: default;
}
button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}
button,
select {
  text-transform: none;
}
textarea {
  overflow: auto;
  vertical-align: top;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
hr {
  display: block;
  margin: 1em 0;
  padding: 0;
  height: 1px;
  border: 0;
  border-top: 1px solid #ccc;
}
small {
  font-size: 80%;
}
html {
  font: 16px/1.5 "Helvetica Neue", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body {
  background-color: #fff;
  color: #000;
}
a {
  color: #000;
  transition-property: color, background-color;
  transition-duration: 150ms;
}
a:focus {
  outline: thin dotted;
}
::selection {
  background: rgba(255,138,0,0.33);
  text-shadow: none;
}
.icon-coffee,
.icon-gulp,
.icon-npm,
.icon-postcss,
.icon-stylus {
  display: inline-block;
  background-image: url("../images/sprite.png");
  vertical-align: middle;
}
.icon-coffee {
  background-position: -252px 0px;
  width: 87px;
  height: 70px;
}
.icon-gulp {
  background-position: -252px -74px;
  width: 45px;
  height: 100px;
}
.icon-npm {
  background-position: 0px -144px;
  width: 128px;
  height: 50px;
}
.icon-postcss {
  background-position: -152px 0px;
  width: 96px;
  height: 96px;
}
.icon-stylus {
  background-position: 0px 0px;
  width: 148px;
  height: 140px;
}
.svg-facebook,
.svg-spinner,
.svg-twitter,
.svg-vkontakte,
.svg-youtube {
  background: url("sprite.css.svg") no-repeat;
}
.svg-facebook {
  background-position: 0 0;
}
.svg-facebook-dims {
  width: 50px;
  height: 50px;
}
.svg-spinner {
  background-position: 50% 0;
}
.svg-spinner-dims {
  width: 50px;
  height: 50px;
}
.svg-twitter {
  background-position: 0 100%;
}
.svg-twitter-dims {
  width: 50px;
  height: 50px;
}
.svg-vkontakte {
  background-position: 50% 100%;
}
.svg-vkontakte-dims {
  width: 50px;
  height: 50px;
}
.svg-youtube {
  background-position: 100% 0;
}
.svg-youtube-dims {
  width: 50px;
  height: 50px;
}
.slick-slider {
  position: relative;
  display: block;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-touch-callout: none;
  -khtml-user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}
.slick-list {
  position: relative;
  display: block;
  overflow: hidden;
  margin: 0;
  padding: 0;
}
.slick-list:focus {
  outline: none;
}
.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}
.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}
.slick-track {
  position: relative;
  top: 0;
  left: 0;
  display: block;
}
.slick-track:before,
.slick-track:after {
  display: table;
  content: '';
}
.slick-track:after {
  clear: both;
}
.slick-loading .slick-track {
  visibility: hidden;
}
.slick-slide {
  display: none;
  float: left;
  height: 100%;
  min-height: 1px;
}
[dir='rtl'] .slick-slide {
  float: right;
}
.slick-slide img {
  display: block;
}
.slick-slide.slick-loading img {
  display: none;
}
.slick-slide.dragging img {
  pointer-events: none;
}
.slick-initialized .slick-slide {
  display: block;
}
.slick-loading .slick-slide {
  visibility: hidden;
}
.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}
.svg-spinner-dims {
  position: relative;
  display: block;
  animation: spin 1s steps(12) infinite;
}
.icon-spinner {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  overflow: hidden;
}
@-moz-keyframes spin {
  100% {
    transform: rotate(360deg);
  }
}
@-webkit-keyframes spin {
  100% {
    transform: rotate(360deg);
  }
}
@-o-keyframes spin {
  100% {
    transform: rotate(360deg);
  }
}
@keyframes spin {
  100% {
    transform: rotate(360deg);
  }
}
body {
  width: 60em;
  margin: 4em auto;
  text-align: center;
}
header,
footer {
  margin: 4em 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
